.Pizza body {
  background: white;
  height: 100vh;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

hr {
  border-top: 5px dotted rgba(230, 226, 225, 0.767);
  border-bottom: none;
  padding-top: 5px;
  width: 60%;
  margin: auto;
}

.cover {
  margin: 0;
  /*background stuff*/
  background-size: 100vh 100vw;
  background-image: url("../img/palace.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  height: 100vh;
  width: 100vw;
}

.cover h1 {
  /*position*/
  padding-top: 10px;
  text-align: center;
  letter-spacing: 0.2rem;
  position: relative;
  position: center;
  margin: 0;
  /*styles*/
  color: rgba(230, 226, 225, 0.979);
  font-size: 7.5vh;
  font-family: "Sacramento", cursive;
}
.cover h2 {
  /*position*/
  margin: 0;
  font-size: 3vh;
  padding-left: 33vw;
  padding-top: 1.5vh;
  position: center;
  /*styles*/
  color: rgba(230, 230, 225, 0.6);
  font-family: "Rubik", sans-serif;
}

/*greeting*/
.cover h3 {
  margin: 0;
  font-family: "Rubik", sans-serif;
  color: rgba(230, 230, 225, 0.6);
  position: relative;
  padding-top: 25vh;
  font-size: 4vh;
  padding-left: 20vw;
  position: center;
}

/*Nav*/
.navbar-global .navbar-user > li > a {
  color: white;
}

.navbar-primary {
  background-color: #924942cc;
  bottom: 0px;
  left: 0px;
  top: 0px;
  position: absolute;
  width: 13vw;
  height: 100vh;
  z-index: 1;
  overflow: hidden;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}
.navbar-primary-menu,
.navbar-primary-menu li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navbar-primary-menu li a {
  display: block;
  margin-top: 25px;
  padding: 30px 5px 30px;
  text-align: left;
  border-bottom: solid 1px rgba(94, 11, 11, 0.664);
  color: rgba(204, 204, 204, 0.849);
  font-family: "Catamaran", sans-serif;
  font-size: 1em;
}

.navbar-primary-menu li a:hover {
  background-color: #000;
  text-decoration: none;
  color: #f2bf80;
  font-size: 1.3em;
  font-family: "Catamaran", sans-serif;
}

.navbar-primary-menu li a .glyphicon {
  margin-right: 1vw;
  font-family: "Catamaran", sans-serif;
}

/*Description*/
.description h1 {
  /*position*/
  padding-top: 20vh;
  padding-left: 0;
  text-align: center;
  letter-spacing: 0.2rem;
  margin: 0;
  /*styles*/
  color: #192965;
  font-size: 6vh;
  font-family: "Arvo", serif;
}

.description p {
  margin: 0;
  /*styles*/
  color: #192965;
  font-size: 2.5vh;
  letter-spacing: 0.15rem;
  font-family: "PT Sans", sans-serif;
}

.description hr {
  margin-left: 300px;
}

.img-vancouver {
  background-size: cover;
  width: 235px;
  height: 235px;
  background-image: url("../img/vancouver.jpg");
  position: cover;
  display: block;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 116, 136, 0.829);
  opacity: 0.8;
  position: absolute;
  margin-left: 2.5vw;
  margin-top: 5vh;
  text-align: border-top;
}

#sub-title {
  position: center;
  padding-top: 350px;
}
.description {
  height: 100vh;
  width: 100vw;
}
.description .container-fluid {
  /*sizing*/
  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(rgba(51, 25, 0, 0.8), rgba(0, 0, 0, 0.3)),
    url("../img/beach.jpg");
  /*background stuff*/
  background-size: 100vh 100vw;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
}
.img-vancouver:hover {
  background-image: url("../img/vancouver.jpg");
  box-shadow: 0 0 20px rgb(0, 116, 136);
  opacity: 1;
}

.description .container-fluid:hover {
  background-image: url("../img/beach.jpg");
  /*background stuff*/
  background-size: 100vh 100vw;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  opacity: 1;
}

.description p {
  margin-left: 10vw;
  margin-top: 5vh;
  margin-bottom: 0;
  text-align: left;
  background-color: rgba(166, 200, 212, 0.555);
}

.description h1 {
  padding-top: 15vh;
  padding-left: 20vw;
  position: center;
}
.description h2 {
  color: #192965;
  font-size: 20px;
  font-family: "Arvo", serif;
}

/*bages*/
.description span {
  display: none;
}
#badges {
  background-color: rgba(0, 0, 0, 0.164);
  color: white;
  width: 8px;
  padding: 20px;
  list-style: none;
}
#badges:hover {
  width: auto !important;
  display: inline-block;
}
#badges:hover span {
  display: inline-block;
}
/*carousel*/
.carouPage {
  height: 100vh;
  width: 100vw;
  padding-top: 25vh;
  background-image: url("../img/firework.jpg");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: 500px 100px;
  background-color: black;
  background-attachment: fixed;
}
.carousel {
  position: relative;
  padding-left: 30px;
  margin: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.carousel-font h1 {
  position: relative;
  padding-left: 3vw;
  padding-top: 30px;
  text-align: center;
  font-size: 2.5em;
  color: white;
  font-family: "Arvo", serif;
}

.carousel-font p {
  position: relative;
  padding-left: 3vw;
  font-size: 1em;
  text-align: center;
  color: white;
  font-family: "PT Sans", sans-serif;
}

.carousel i {
  font-size: 4.5vw;
}
.slide {
  position: relative;
  min-width: 100%;
  height: 50vh;
  transition: 0.5s;
}

#goLeft {
  position: absolute;
  top: 50%;
  left: 30px;
  transform: translateY(-50%);
  width: 10%;
  height: 60%;
  background: none;
  border: none;
  outline: none;
  transition: 0.5s;
  color: white;
  background: rgba(0, 0, 0, 0.384);
}

#goLeft:hover {
  background: rgba(255, 192, 203, 0.418);
}

#goRight {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 10%;
  height: 60%;
  right: 0;
  background: rgba(0, 0, 0, 0.384);
  color: white;
  border: none;
  outline: none;
}

#goRight:hover {
  background: rgba(255, 192, 203, 0.418);
}

.carousel a {
  color: #f2bf80;
  font-size: 1em;
  text-align: center;
  position: relative;
  text-decoration: underline;
  padding-top: 0;
  padding-left: 3px;
}

p {
  text-align: center;
}

/*timeline component */
#timelineTitle {
  margin-top: 0;
}
.timeline {
  padding-top: 100px;
  background-color: linear-gradient(rgba(51, 25, 0, 0.8), rgba(0, 0, 0, 0.3));
  background-image: url("../img/plain.jpg");
  background-size: cover;
}
.timeline h1 {
  padding-left: 100px;
  padding-bottom: 20px;
  padding-top: 30px;
  color: #5d5b6a;
  font-family: "Arvo", serif;
  text-align: center;
}
.timeline-container {
  padding-top: 0px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.timeline-container::after {
  padding-top: 0;
  background-color: #71c9ce;
  content: "";
  position: absolute;
  left: calc(50% - 2px);
  width: 10px;
  height: 100%;
}

.time-item {
  margin-top: 30px;
  display: flex;
  justify-content: flex-end;
  padding-right: 30px;
  margin: 10px 0;
  width: 50%;
}
.time-item h1 {
  text-align: center;
  color: #5d5b6a;
  padding-top: 50px;
}

.time-item:nth-child(odd) {
  align-self: flex-end;
  justify-content: flex-start;
  padding-right: 0px;
  padding-left: 30px;
}
#root {
  width: 100%;
}
.time-item-content {
  background-color: #fae3d9;
  border-radius: 5px;
  border-width: 10px;
  box-shadow: 0 0 30px rgba(228, 211, 211, 0.822);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
  padding: 15px;
  max-width: 70%;
  width: 400px;
  text-align: right;
}
.time-item-content time {
  font-size: 1em;
  color: #3f72af;
  padding-left: 0;
  font-family: cursive;
}
.time-item-content h2 {
  font-size: 1em;
  color: #3f72af;
  padding-left: 0;
  font-family: sans-serif;
}

.time-item-content::after {
  content: " ";
  background-color: #a6e3e9;
  box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.521);
  width: 15px;
  height: 15px;
  position: absolute;
  transform: rotate(45deg);
  right: -7.5px;
  top: calc(50% - 7.5px);
}
.time-item:nth-child(odd) .time-item-content {
  align-items: flex-start;
  text-align: left;
}

.time-item:nth-child(odd) .time-item-content::after {
  box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.5);
  right: auto;
  left: -7.5px;
}

.time-item-content .tag {
  color: white;
  font-size: 12px;
  font-weight: bold;
  padding: 5px;
  position: absolute;
  top: 5px;
  left: 4px;
  text-transform: uppercase;
}

.time-item:nth-child(odd) .time-item-content .tag {
  left: auto;
  right: 5px;
}

.time-item-content .time {
  color: #777;
  font-size: 12px;
}

.time-item-content p {
  color: #48466d;
  font-size: 13px;
  line-height: 24px;
  margin: 15px, 0;
  max-width: 250px;
  position: center;
}
.time-item-content a {
  color: #333;
  text-decoration: none;
  font-size: 14px;
}
.time-item-content a::after {
  content: ">";
  font-size: 12px;
}

.time-item-content .circle {
  background-color: #e3fdfd;
  border: 5px solid #11999e;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 10px);
  right: -40px;
  width: 20px;
  height: 20px;
  z-index: 100;
}

.time-item:nth-child(odd) .time-item-content .circle {
  right: auto;
  left: -40px;
}

@media only screen and (max-width: 767px) {
  .time-item-content,
  .timeline-item:nth-child(odd) .time-item-content {
    padding: 15px 10px;
    text-align: center;
    align-items: center;
  }
  .time-item-content .tag {
    width: calc(100% - 10px);
  }

  .time-item-content time {
    margin-top: 20px;
  }

  .time-item-content a {
    text-decoration: underline;
  }

  .time-item-content a::after {
    display: none;
  }
}

